import React, { Component } from "react"
import { Row, Col, Button, Menu, Dropdown, Icon } from "antd"
import { Link } from "react-router-dom"
import socket from "../transport/Connect"
const licens = new WebSocket("wss://testlic-1.ymess.org:5005")

class Auth extends Component {
  constructor() {
    super()
    this.state = {
      nodes: [],
      activeNode: null
    }
  }

  componentDidMount() {
    const thiss = this

    licens.onopen = () => {
      // console.log("Соединение с лицензиаром установлено")
      const getNodesObj = {
        RequestId: socket.makeId(),
        RequestType: 1,
        Type: 6
      }

      licens.send(JSON.stringify(getNodesObj))
    }

    licens.onmessage = event => {
      const response = socket.blobToJson(event.data)
      if (response.ResponseType === 2) {
        thiss.setState({
          nodes: response.Nodes
        })
        // устанавливаем первую ноду по умолчанию если выбран режим "автоматически"
        if (!localStorage.getItem("node")) {
          localStorage.setItem("node", JSON.stringify(response.Nodes[0]))
        }
      }
    }
  }

  selectNode(node) {
    this.setState({
      activeNode: node.item.props.data
    })
    localStorage.setItem("node", JSON.stringify(node.item.props.data))
  }

  render() {
    const menu = (
      <Menu onClick={this.selectNode.bind(this)}>
        {this.state.nodes.map((node, i) => {
          return (
            <Menu.Item key={i} data={node}>
              {node.Name}
            </Menu.Item>
          )
        })}
        {
          // this.state.activeNode ? <Menu.Item key='auto' data={null}>Автоматически</Menu.Item> : null
        }
      </Menu>
    )
    return (
      <Row
        type="flex"
        align="middle"
        justify="center"
        style={{ height: "100vh" }}
      >
        <Col>
          <div className="auth__type">
            <h1>Вход</h1>
            <h2>Выберите способ входа</h2>
            <Link to={this.props.match.url + "/by-phone"}>
              <Button type="secondary">По номеру телефона</Button>
            </Link>
            <br />
            <Link to={this.props.match.url + "/by-email"}>
              <Button type="secondary">По электронной почте</Button>
            </Link>
            <br />
            <Button type="secondary">Через приложение</Button>
            <br />
            <br />
            <Link to={"/registration"}>Регистрация</Link>
            <br />
            <div className="auth__type_server">
              <span>Сервер: </span>
              <Dropdown overlay={menu}>
                <span className="ant-dropdown-link">
                  {localStorage.getItem("node")
                    ? JSON.parse(localStorage.getItem("node")).Name
                    : "Автоматически"}
                  <Icon type="down" />
                </span>
              </Dropdown>
            </div>
          </div>
        </Col>
      </Row>
    )
  }
}

// Auth.defaultProps = {
//    img: 'https://223421.selcdn.ru/chatix-dev/assets/img/default_avatar.png'
// }

export default Auth
