import React, { Component } from "react"
import { Row, Col, Button, Form, Input } from "antd"
import socket from "../transport/Connect"

class Verify extends Component {
  constructor() {
    super()
    this.state = {
      verifyUid: null,
      verifyErr: null,
      verifyRequestSended: false,
      verifyCode: null,
      verifyCodeErr: null,
      btnPreloader: false,
      verifyType: ""
    }
  }

  componentDidMount() {
    const path = this.props.location.pathname.split('/');
    this.setState({
      verifyType: path[2]
    })

    socket.onMessage = data => {
      this.setState({
        btnPreloader: false
      })

      if (data.ErrorCode !== 0) {
        this.setState({
          verifyErr: data.Message
        })
      } else if (data.ResponseType === 10) {
        this.setState({
          verifyRequestSended: true
        })
      } else if (data.ResponseType === 6) {
        localStorage.setItem("FileAccessToken", data.FileAccessToken)
        localStorage.setItem("AccessToken", data.Token.AccessToken)
        localStorage.setItem("RefreshToken", data.Token.RefreshToken)
        localStorage.setItem("User", JSON.stringify(data.User))

        window.location.replace("/")
      }
    }
  }

  onChangePhone(e) {
    this.setState({
      verifyUid: e.target.value,
      verifyErr: null
    })
  }

  onChangeVerifCode(e) {
    this.setState({
      verifyCode: e.target.value,
      verifyCodeErr: null
    })
  }

  sendverifyCode(e) {
    e.preventDefault()

    this.setState({
      btnPreloader: true
    })

    const data = {
      Uid: this.state.verifyUid,
      VCode: this.state.verifyCode,
      LoginType: 1,
      UidType: this.state.verifyType === 'by-email' ? 2 : this.state.verifyType === 'by-phone' ? 0 : 0,
      RequestId: socket.makeId(),
      RequestType: 33,
      Type: 0
    }

    socket.send(data)
  }

  sendVerificationQuery(e) {
    e.preventDefault()

    this.setState({
      btnPreloader: true
    })

    const data = {
      VerificationType: this.state.verifyType === 'by-email' ? 1 : this.state.verifyType === 'by-phone' ? 0 : 0,
      Uid: this.state.verifyUid,
      RequestId: socket.makeId(),
      RequestType: 42,
      Type: 0
    }

    socket.send(data)
  }

  render() {
    return (
      <Row
        type="flex"
        align="middle"
        justify="center"
        style={{ height: "100vh" }}
      >
        <Col>
          <div className="auth__type">
            <h1>Вход</h1>
            {!this.state.verifyRequestSended ? (
              <div>
                <h2>
                  {this.state.verifyType === 'by-email' ? 'Введите ваш email' : this.state.verifyType === 'by-phone' ? 'Введите номер телефона' : ''}
                </h2>
                <Form
                  onSubmit={this.sendVerificationQuery.bind(this)}
                  className="login-form"
                >
                  <Form.Item
                    validateStatus={this.state.verifyErr ? "error" : ""}
                    help={this.state.verifyErr ? this.state.verifyErr : ""}
                  >
                    <Input
                      placeholder={this.state.verifyType === 'by-email' ? 'Ваш email' : this.state.verifyType === 'by-phone' ? 'Ваш номер телефона' : ''}
                      onChange={this.onChangePhone.bind(this)}
                    />
                  </Form.Item>
                  <Form.Item>
                    <Row>
                      <Col style={{ textAlign: "center" }}>
                        <Button
                          type="primary"
                          htmlType="submit"
                          disabled={!this.state.verifyUid}
                          loading={!!this.state.btnPreloader}
                        >
                          Получить {this.state.verifyType === 'by-email' ? 'письмо' : this.state.verifyType === 'by-phone' ? 'SMS' : ''} с кодом
                        </Button>
                      </Col>
                    </Row>
                  </Form.Item>
                </Form>
              </div>
            ) : (
              <div>
                <h2>Код подтверждения из {this.state.verifyType === 'by-email' ? 'письма' : this.state.verifyType === 'by-phone' ? 'SMS' : ''}</h2>
                <p>
                  Мы отправили {this.state.verifyType === 'by-email' ? 'письмо' : this.state.verifyType === 'by-phone' ? 'SMS' : ''} 
                  на {this.state.verifyType === 'by-email' ? 'email' : this.state.verifyType === 'by-phone' ? 'номер' : ''} {this.state.verifyUid}. Введите
                  полученый код в поле ниже
                </p>
                <Form
                  onSubmit={this.sendverifyCode.bind(this)}
                  className="login-form"
                >
                  <Form.Item
                    validateStatus={this.state.verifyCodeErr ? "error" : ""}
                    help={this.state.verifyCodeErr ? this.state.verifyCodeErr : ""}
                  >
                    <Input
                      placeholder={`Код из ${this.state.verifyType === 'by-email' ? 'письма' : this.state.verifyType === 'by-phone' ? 'SMS' : ''}, 4 цифры`}
                      onChange={this.onChangeVerifCode.bind(this)}
                    />
                  </Form.Item>
                  <Form.Item>
                    <Row>
                      <Col style={{ textAlign: "center" }}>
                        <Button
                          type="primary"
                          htmlType="submit"
                          disabled={!this.state.verifyCode}
                          loading={!!this.state.btnPreloader}
                        >
                          Войти
                        </Button>
                      </Col>
                    </Row>
                  </Form.Item>
                </Form>
              </div>
            )}
          </div>
        </Col>
      </Row>
    )
  }
}

export default Verify
